import { useStaticQuery, graphql } from "gatsby"

const useSiteMetadata = () => {


  const data = useStaticQuery(graphql`
    query ($title: String) {
     allMdx(filter: {frontmatter: {title: {eq: $title}}}) {
    nodes {
      frontmatter {
        title
      }
    }
  }
  }
  `)


  // MDX    .
  return data
}

export default useSiteMetadata